
import { localStore } from '../utils/localStore';
import { removeFromOptions } from '../utils/quicklinksBuilder';

const STORAGE_KEY = 'SIMKL-MAL-SYNC';
export default {
  data: () => ({
    streamUrl: undefined as undefined | string,
    continueUrl: null,
    resumeUrl: null,
    links: null as any,
    pageSearch: null as any,
    pageRelation: null as any,
    classes: {
      minimized: true,
      search: false,
    },
  }),
  watch: {
    streamUrl(url) {
      if (url) {
        this.classes.minimized = true;
        this.classes.search = false;
      }
    },
  },
  created() {
    const classes = JSON.parse(localStore.getItem(STORAGE_KEY)!);
    if (classes) {
      this.classes = classes;
    }
  },
  methods: {
    lang: api.storage.lang,
    favicon: utils.favicon,
    assetUrl: api.storage.assetUrl,
    getMal2KissFavicon(url) {
      try {
        return utils.favicon(url);
      } catch (e) {
        con.error(e);
        return '';
      }
    },
    toggleSearch() {
      this.classes.search = !this.classes.search;
      this.saveClasses();
    },
    pressMinimized() {
      if (this.links === null || Object.keys(this.links).length) {
        this.toggleMinimized();
      } else {
        this.toggleSearch();
      }
      this.saveClasses();
    },
    toggleMinimized() {
      this.classes.minimized = !this.classes.minimized;
      if (this.classes.search && this.classes.minimized) this.toggleSearch();
      this.saveClasses();
    },
    removeSource(key) {
      removeFromOptions(String(key.name));
      window.location.reload();
    },
    saveClasses() {
      localStore.setItem(STORAGE_KEY, JSON.stringify(this.classes));
    },
  },
};
